.square {
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: rgb(70, 70, 70);
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 5%;
  font-size: 100px;
}

@media (max-width: 550px) {
  .square {
    font-size: 70px;
  }
}



