#board {
  width: 500px;
  height: 500px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 20px;
}

@media (max-width: 550px) {
  #board {
    width: 300px;
    height: 300px;
    grid-gap: 12px;
  }
}



